@import '../../styles/utils/variables';
#Contact {
	width: 100%;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	gap: 20px;
	background-color: $background-color;

	background-image: url('https://www.transparenttextures.com/patterns/clean-textile.png');
	box-shadow: lightgray 4px 4px 20px;

	padding-top: 25px;
	padding-bottom: 35px;

	input[type='text'],
	input[type='email'],
	textarea,
	select {
		width: 100%;
		padding: 12px 20px;
		margin: 8px 0;
		display: inline-block;
		border: 1px solid #ccc;
		border-radius: 4px;
		box-sizing: border-box;
	}
	h2 {
		font-size: 34px;
		font-weight: bold;
		color: $primary-color;
	}
	textarea {
		height: 150px;
	}
	input[type='submit'] {
		width: 100%;
		background-color: $primary-color;
		color: white;
		padding: 14px 20px;
		margin: 8px 0;
		border: none;
		border-radius: 4px;
		cursor: pointer;
	}

	input[type='submit']:hover {
		background-color: $primary-color;
	}

	.nameMail {
		display: flex;
		flex-direction: row;
		align-items: center;
		gap: 10px;
	}
	.smallInput {
		flex: 1;
		display: flex;
		flex-direction: column;
	}
	.mailStatus {
		text-align: center;
	}
	form {
		display: flex;
		flex-direction: column;
		min-width: 25%;
		padding: 15px;
		border-radius: 10px;
		background-color: $background-color;
		box-shadow: lightgray 4px 4px 20px;
	}
}
.invisible {
	display: none;
}
.success {
	color: green;
}
.fail {
	color: red;
}
